<template>
	<div id="member-phonic">
		<public-header title="语音设置"></public-header>
		<div class="content">
			<div class="padding-10-tb padding-15-lr font-14 c-gray">新语音提示</div>
			<van-cell-group>
				<van-cell title="语音提示">
					<iswitch :value="phonic.voice" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="phonic.voice"></iswitch>
				</van-cell>
				<van-cell title="新消息振动">
					<iswitch :value="phonic.vibrate" slot="right-icon" text-open="ON" text-close="OFF" @change="jsToggleSwitch" keys="phonic.vibrate"></iswitch>
				</van-cell>
			</van-cell-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">铃声</div>
			<van-radio-group v-model="phonic.voice_type">
				<van-cell-group>
<!--
					<van-cell title="音乐" clickable @click="phonic.voice_type = 2">
						<van-radio :name="2" />
					</van-cell>
-->
					<van-cell title="订单内容" clickable @click="phonic.voice_type = 1">
						<van-radio :name="1" />
					</van-cell>
				</van-cell-group>
			</van-radio-group>
			<div class="padding-10-tb padding-15-lr font-14 c-gray">订单通知次数</div>
			<van-cell-group>
				<van-cell title="外卖新订单">
					<div class="flex" slot="right-icon" @click="onToggleTimes('ordernew')">
						<span class="c-gray">通知{{phonic.voice_data.ordernew.times}}次</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
<!--
				<van-cell title="外卖退款单">
					<div class="flex" slot="right-icon" @click="onToggleTimes('orderrefund')">
						<span class="c-gray">通知{{phonic.voice_data.orderrefund.times}}次</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
-->
			</van-cell-group>
			<div class="padding-15">
				<van-button type="primary" :disabled="!islegal" size="normal" block class="font-16" @click="onSubmit">保存</van-button>
			</div>
		</div>
		<van-popup v-model="timesPopup" position="bottom">
			<div class="popup-content">
				<van-picker :columns="times" @cancel="onToggleTimes" @confirm="onConfirmTimes" show-toolbar title="播放次数"/>
			</div>
		</van-popup>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import iswitch from "@/components/switch"
	import cloudApp from "@/config/cloudapp.js"
	export default {
		components: {
			publicHeader,
			iswitch
		},
		data() {
			return {
				phonic: {
					voice: 0,
					vibrate: 0,
					voice_type: 0,
					voice_data: {
						ordernew: {
							times: 1
						},
						orderrefund: {
							times: 1
						},
					}
				},
				timesPopup: false,
				times: [0, 1, 3, 5],
				keySelected: '',
				islegal: false,
				showPreLoading: true
			}
		},
		methods: {
			onLoad() {
				let phonic = cloudApp.igetPrefs('ipphonic');
				this.phonic = Object.assign(this.phonic, phonic);
				this.islegal = true;
				this.showPreLoading = false;
			},
			onSubmit() {
				if(!this.islegal) {
					return false;
				}
				this.islegal = false;
				cloudApp.isetPrefs('ipphonic', this.phonic);
				this.util.$toast('设置成功', this.util.getUrl({path: 'pages/member/mine'}), 1000, 'replace');
			},
			onToggleTimes(key) {
				if(key) {
					this.keySelected = key;
				}
				this.timesPopup = !this.timesPopup;
			},
			onConfirmTimes(value, index) {
				this.phonic.voice_data[this.keySelected].times = value;
				this.onToggleTimes();
			},
			jsToggleSwitch(res) {
				this.util.jsToggleSwitch({
					vue: this,
					key: res.key || res.keys,
					value: res.value
				});
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#member-phonic .iswitch{
		height: 24px;
	}
	#member-phonic .van-picker__cancel{
		color: #7a7a7a!important;
	}
	#member-phonic .van-picker__confirm{
		color: #4FAE52;
	}
</style>


